@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Monoton&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



:root {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  font-family: 'Architects Daughter', cursive;
  color: white;
  background-color: rgba(0,0,0,.5);
  overflow: auto;
  position: relative;
  z-index: 1;
   font-size: 62.5%;
}

body{
  width: 100%;
}
.background-container{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  z-index: 10;
}
.background-container img{
  width: 100%;
  min-height: 100vh;
  object-fit: fill;
  overflow: hidden;
}
.game-container{
  width: 100%;
  min-height: 100vh;
  position: relative;
  z-index: 20;
  padding:3vh 5vh;
  background-color: rgba(0,0,0,0.5);
  box-shadow: 10px 10px rgb(0 0 0 / 25%);
  min-width: 100vw;
}
header{
  padding: 1em 0;
  text-align: center;
  font-family: 'Monoton', cursive;
  color: #fb5959;
}
header h1{
  letter-spacing: .34rem;
  font-size: 4rem;
  text-transform: uppercase;
  line-height: 1;
}
header h3{
  letter-spacing: .30rem;
  font-size: 3.2rem;
  line-height: 1.1;
}
.board-container{
  width: 100%;
  padding: 1em 0;
}

.board-container.win{
  display: flex;
}

.squares{
  padding: 2.5em 0;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  max-width: 350px;
  max-height: 100px;
  
}
.squares button{
  max-width: 115px;
  width: 33%;
  min-height: 85px;
  display: flex;
  align-items: center;
  background: none;
  justify-content: center;
  border: 2px solid white;
  font-size: 1rem;
  position: relative;
}
.squares button img{
  width: 33%;
  -webkit-transform: scale(2.5) ;
          transform: scale(2.5) ;
}
.active{
  width: 100%;
  max-width: 20px;
  min-height: 20px;
  display: block;
}
.not-active{
  display: none;
}
.game-manage{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contestant-score{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  padding: 1rem 0;
  line-height: 1.4;
}

.contestant-score > p{
  font-size: 1.8rem;
}
.score{
  max-width: 375px;
  width: 100%;
  padding: .5em 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-size: 2.2rem;
}
.score h3{
  max-width: -webkit-max-content;
  max-width: max-content;
  margin: 0 5px 0 0;
  border-bottom: 2px solid #ffffff;

}
.new-game-btn{ 
  margin: 1em 0 0;
  padding:0.5em 1.25em;
  font-size: 1.3rem;
  background: none;
  border:none;
  background-color: #fb5959;
  color: #ffffff;
  font-family: 'Monoton', cursive;
}
.new-game-btn:hover{
  scale: 1.5;
  background-color: #fb59599d;
  border:1px solid white;
}
.winner-container{
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  align-items: center;
  font-size: 2.5rem;
  line-height: 1.4;
  letter-spacing: .2rem;
  font-weight: 700;
  max-width: 350px;
  text-align: center;
}
.winner-container h2{
  font-size: 3.5rem;
  line-height: 1;
}



@media (max-width: 521px) {

  html{
    font-size: 55%;
  }
  .game-container{
    background-color: rgba(0,0,0,0.25);
  }
  .winner-container{
    padding:1em 0 0;
  }
  .score{
    flex-direction: column;
  }
  .squares button img{
    -webkit-transform: scale(1.5) rotate(965deg) rotate3d(1, 1, 1, 45deg);
            transform: scale(1.5) rotate(965deg) rotate3d(1, 1, 1, 45deg);
  }

}
@media (min-width: 522px) and (max-width: 1024px) {

  html{
    font-size: 58.5%;
  }
  

}
@media (orientation: landscape) and (max-width: 812px){
  .game-container{
    min-height: 110vh;
    max-height: 110vh;
  }
  header h1{
    font-size: 3rem;
  }
  header h3{
    font-size: 2rem;
  }
  .board-container{  
      display: flex;
      justify-content: space-evenly;
      padding: 2vh 4vw;
      }
  .squares {
    width: 40%;
    max-width: 275px;
  }
  .squares button{
    min-height: 65px;
  }
  .squares button img{
    -webkit-transform:scale(2) rotate(965deg) rotate3d(1, 1, 1, 45deg);
            transform:scale(2) rotate(965deg) rotate3d(1, 1, 1, 45deg);
  }
  .winner-container{
    width: 40%;
    margin: 0 auto;
    font-size: 1.5rem;

  }
  .winner-container h2 {
      font-size: 2rem;
      line-height: 1.3;
  }
}
